const tranceServer = '/yss-base-billows';
const ruleServer = '/rule-engine';

// 
//  
export async function manualAdd(params) {
  return ajax.post(serverPre(`${manualServerName}/save`), params);
}

//  
export async function manualUpdate(params) {
  return ajax.post(serverPre(`${manualServerName}/update`), params);
}

//  
export async function manualDelete(id) {
  return ajax.post(serverPre(`${manualServerName}/delete/${id}`));
}

