import { Message } from 'element-ui';
import {
  processDetail,
  processAdd,
  processEdit,
  getFlowInstanceVariables,
  editFormProcess
} from '@api/processConfig';
import { getByTemplateId } from '@api/customForm';
import { dataViewFields } from '@api/viewData';
import cloneDeep from 'lodash/cloneDeep';
import Base64 from '@utils/Base64';
import { arrayFind } from '@utils/util';

// tab,obj
function assignObj(objectPool, params) {
  const obj = {};
  const keyList = Object.keys(objectPool);
  // 
  if (!keyList.length) {
    obj[params.id] = params.attrObj;
  } else {
    // 
    const NodeIndex = keyList.indexOf(params.id);
    if (NodeIndex === -1) {
      obj[params.id] = params.attrObj;
    } else {
      if (params.replace) {
        obj[params.id] = params.attrObj;
      } else {
        const oldValue = objectPool[params.id];
        //
        for (const key in params.attrObj) {
          if (Object.prototype.hasOwnProperty.call(params.attrObj, key)) {
            if (
              oldValue[key] &&
              typeof params.attrObj[key] === 'object' &&
              !(params.attrObj[key] instanceof Array)
            ) {
              const assignSubObj = Object.assign(
                oldValue[key],
                params.attrObj[key]
              );
              oldValue[key] = assignSubObj;
            } else {
              oldValue[key] = params.attrObj[key];
            }
          }
        }
        obj[params.id] = oldValue;
      }
    }
  }
  return obj;
}

const processTemplate = {
  namespaced: true,
  state: {
    currentStatus: 'edit', // edit-tabvuexcreate-
    currentNodeId: '', // id
    currentTempId: '', // idtab
    tempDetailInfo: {}, // 
    objectPool: {}, // 
    updateNameByWhere: 'none', // tabwatch
    generalProperties: {}, // 
    globalVariable: [], // 
    manualLaunch: [],
    timingLaunch: [],
    instructionLaunch: [],
    activeName: 'canvas',
    canvasXml: '', // xml
    userTaskList: [], // 
    form: [], // 
    customForm: {
      // 
      formList: []
    },
    formItem: '', // base64
    allFieldsData: [],
    monitorTaskData: [], //
    stationListArr: [], //
    flowInstanceVariables: [],
    selectChange: false,
    tableFormList: [],

    pageFresh: false, // 
    instructionFormv: false, //   
    manualForm: false, //   
    timingForm: false //   
  },
  getters: {
    showPublicAttrData: (state) => {
      const tempObj = state.objectPool[state.currentNodeId];
      if (tempObj) {
        return {
          name: tempObj.name ? tempObj.name : '',
          documentation: tempObj.documentation ? tempObj.documentation : ''
        };
      }
    },
    showtimeOutData: (state) => {
      const tempObj = state.objectPool[state.currentNodeId];
      if (tempObj) {
        return {
          timeoutSettings: tempObj.timeoutSettings
            ? tempObj.timeoutSettings
            : {},
          isTimeoutSettings: tempObj.isTimeoutSettings
            ? tempObj.isTimeoutSettings
            : 'false'
        };
      }
    },
    showDealSetData: (state) => {
      const tempObj = state.objectPool[state.currentNodeId];
      if (tempObj) {
        return {
          transactorGroup: tempObj.transactorGroup
            ? tempObj.transactorGroup
            : '',
          defaultTransactor: tempObj.defaultTransactor
            ? tempObj.defaultTransactor
            : '',
          handlingStrategy: tempObj.handlingStrategy
            ? tempObj.handlingStrategy
            : '',
          countersignatureStrategy: tempObj.countersignatureStrategy
            ? tempObj.countersignatureStrategy
            : '',
          noCorrespondingHandler: tempObj.noCorrespondingHandler
            ? tempObj.noCorrespondingHandler
            : '',
          operationAuthority: tempObj.operationAuthority
            ? tempObj.operationAuthority
            : [],
          isArriveOnTime: tempObj.isArriveOnTime
            ? tempObj.isArriveOnTime
            : 'false',
          handlingTime: tempObj.handlingTime ? tempObj.handlingTime : ''
        };
      }
    },
    flowVariables: (state) => {
      /**
       * 
       *   4
       * * 
       * * *  -- textvalueform
       * * *  -- textlabelvalueformItem
       *
       * * 
       * * *  -- textvalueglobalVariable
       * * *  -- textvaluecode
       * * *  -- textvaluecode
       *
       * * 4
       * * *  -- textvaluenodeVariable
       * * *  -- textvaluekey
       * * *  -- textvalueid
       * * *  -- textvaluecode
       *
       * * 2
       * * *  -- textvalueflowInstance
       * * * value
       * * * * -- textidvalueinstanceId
       * * * * -- textidvaluetaskId
       */

      let formCdn,
        globalVariableCdn,
        nodeVariableCdn,
        flowInstanceCdn,
        monitorTaskCdn;
      function formFun() {
        formCdn = [];
        formFunRecursion(state.customForm.formList, formCdn, null);
        delChildren(formCdn);
        function formFunRecursion(list, arr, parentKey) {
          list.forEach((item) => {
            let fieldType = 0;
            switch (item.type) {
              case 'upload':
                fieldType = 5;
                break;
              case 'date':
                fieldType = 2;
                break;
              case 'number':
                fieldType = 1;
                break;
              case 'hidden':
                fieldType = 6;
                break;
            }
            const vars = {
              text: item.label,
              label: item.label,
              value: parentKey
                ? `formVariable.${parentKey}.${item.key}`
                : `formVariable.${item.key}`,
              fieldType,
              children: []
            };

            if (item.type === 'table') {
              if (item.origId) {
                vars.origId = item.origId;
              }
              vars.children = item.cols.map((col) => {
                return {
                  ...col,
                  fieldCode: `formVariable.${item.key}.${col.prop}`,
                  value: parentKey
                    ? `formVariable.${parentKey}.${item.key}.${col.prop}`
                    : `formVariable.${item.key}.${col.prop}`,
                  fieldType: item.fieldType === 'date' ? 2 : 0
                };
              });
            }
            if (item.type === 'childFrom' && item.value.formList) {
              formFunRecursion(item.value.formList, vars.children, item.key);
            }
            arr.push(vars);
          });
        }
        function delChildren(arr) {
          arr.forEach((item) => {
            if (
              Object.prototype.hasOwnProperty.call(item, 'children') &&
              item.children.length > 0
            ) {
              delChildren(item.children);
            } else {
              delete item.children;
            }
          });
        }
      }
      function globalVariableFun() {
        globalVariableCdn = [];
        state.globalVariable.forEach((item) => {
          const preCode = `globalVariable.${item.varCode}`;
          const node = {
            text: item.varName,
            label: item.varName,
            value: preCode
          };
          if (item.dataViewId) {
            const dataView = arrayFind(
              state.allFieldsData,
              'id',
              item.dataViewId
            );
            node.origId = item.dataViewId;
            if (dataView) {
              node.children = dataView.fields.map((fieldItem) => {
                return {
                  text: fieldItem.fieldName,
                  label: fieldItem.fieldName,
                  value: `${preCode}.${fieldItem.fieldCode}`,
                  fieldType: fieldItem.fieldType
                };
              });
            }
          }

          globalVariableCdn.push(node);
        });
      }

      function nodeVariableFun() {
        const nodeVariableCdnAll = [];
        const data = state.userTaskList.concat(state.monitorTaskData);
        for (let i = 0; i < data.length; i++) {
          const arr = [];
          filStatus(data[i], 'initEvent', arr);
          filStatus(data[i], 'startEvent', arr);
          filStatus(data[i], 'completionEvent', arr);
          filStatus(data[i], 'autoCompletionEvent', arr);
          filStatus(data[i], 'deleteEvent', arr);
          filStatus(data[i], 'rejectEvent', arr);
          filStatus(data[i], 'endEvent', arr);
          filStatus(data[i], 'retakeCustomEvent', arr);
          nodeVariableCdnAll.push({
            text: data[i]['name'],
            label: data[i]['name'],
            value: data[i]['id'],
            children: arr
          });
        }
        function filStatus(dataI, index, arr) {
          if (Object.prototype.hasOwnProperty.call(dataI, index)) {
            dataI[index].forEach((item) => {
              item = JSON.parse(item);
              if (item.type * 1 === 2) {
                const preCode = `interactiveVariable.${item.code}`;
                if (!arrayFind(arr, 'value', preCode)) {
                  const ps = arrayFind(
                    state.allFieldsData,
                    'id',
                    item.serviceValue
                  );
                  if (ps) {
                    const viewArr = ps.fields.map((field) => {
                      return {
                        text: field.fieldName,
                        label: field.fieldName,
                        value: `${preCode}.${field.fieldCode}`,
                        fieldType: field.fieldType
                      };
                    });
                    arr.push({
                      text: item.tempName,
                      label: item.tempName,
                      value: preCode,
                      children: viewArr,
                      origId: item.serviceValue
                    });
                  }
                }
              }
            });
          }
        }
        nodeVariableCdn = nodeVariableCdnAll.filter(
          (item) => item.children.length > 0
        );
      }

      function monitorTaskFun() {
        monitorTaskCdn = [];
        state.monitorTaskData.forEach((item) => {
          const configParams = cloneDeep(
            JSON.parse(Base64.decode(item.config))
          );
          if (configParams.dataViewId) {
            const preCode = `interactiveVariable.${
              item.code ? item.code : item.id
            }`;
            const node = {
              text: item.name,
              label: item.name || item.id,
              value: preCode
            };

            const dataView = arrayFind(
              state.allFieldsData,
              'id',
              configParams.dataViewId
            );
            if (dataView) {
              node.origId = configParams.dataViewId;
              node.children = dataView.fields.map((fieldItem) => {
                return {
                  text: fieldItem.fieldName,
                  label: fieldItem.fieldName,
                  value: `${preCode}.${fieldItem.fieldCode}`,
                  fieldType: fieldItem.fieldType
                };
              });
            }
            monitorTaskCdn.push(node);
          }
        });
      }

      function flowInstanceFun() {
        flowInstanceCdn = [...state.flowInstanceVariables];
      }
      formFun();
      globalVariableFun();
      nodeVariableFun();
      flowInstanceFun();
      monitorTaskFun();

      const launchVaribles = [];
      const trans = (lau) => {
        const preCode = `interactiveVariable.${lau.handCode}`;
        let viewArr = [];
        if (!lau['accessFlags']) {
          const ps = arrayFind(state.allFieldsData, 'id', lau.dataViewId);
          if (ps) {
            viewArr = ps.fields.map((field) => {
              return {
                text: field.fieldName,
                label: field.fieldName,
                value: `${preCode}.${field.fieldCode}`,
                fieldType: field.fieldType
              };
            });
          }
        } else {
          // 
          if (lau.parameters) {
            viewArr = lau.parameters.map((param) => {
              return {
                text: param.name,
                label: param.name,
                value: `${preCode}.${param.code}`,
                fieldType: param.type ? param.type : 0
              };
            });
          }
        }
        if (viewArr.length > 0) {
          launchVaribles.push({
            text: lau.handName,
            label: lau.handName,
            value: preCode,
            origId: lau.dataViewId,
            children: viewArr
          });
        }
      };

      state.manualLaunch.forEach(trans);
      state.timingLaunch.forEach(trans);
      state.instructionLaunch.forEach(trans);

      const flowVariablesArr = [];
      if (formCdn.length > 0) {
        flowVariablesArr.push({
          text: '',
          label: '',
          value: 'form',
          children: formCdn
        });
      }
      if (globalVariableCdn.length > 0) {
        flowVariablesArr.push({
          text: '',
          label: '',
          value: 'globalVariable',
          children: globalVariableCdn
        });
      }
      if (nodeVariableCdn.length > 0) {
        flowVariablesArr.push({
          text: '',
          label: '',
          value: 'nodeVariable',
          children: nodeVariableCdn
        });
      }
      if (launchVaribles.length > 0) {
        flowVariablesArr.push({
          text: '',
          label: '',
          value: 'launchVaribles',
          children: launchVaribles
        });
      }
      if (monitorTaskCdn.length > 0) {
        flowVariablesArr.push({
          text: '',
          label: '',
          value: 'interactiveVariable',
          children: monitorTaskCdn
        });
      }
      if (flowInstanceCdn.length > 0) {
        flowVariablesArr.push({
          text: '',
          label: '',
          value: 'flowInstance',
          children: flowInstanceCdn
        });
      }
      return flowVariablesArr;
    }
  },
  mutations: {
    UPDATE_MONITOR_TASK: (state, params) => {
      state.monitorTaskData = params;
    },
    UPDATE_STATION_LIST: (state, params) => {
      state.stationListArr = params;
    },
    UPDATE_CANVAS_XML: (state, params) => {
      state.canvasXml = params;
    },
    SWITCH_TAB: (state, activeName) => {
      state.activeName = activeName;
    },
    USERTASK_NODE_LIST: (state, nodeList) => {
      state.userTaskList = nodeList;
    },
    UPDATE_POOL: (state, attr) => {
      state.objectPool = cloneDeep(Object.assign(state.objectPool, attr));
    },
    UPDATE_EDITFLAG: (state, flag) => {
      state.updateNameByWhere = flag;
    },
    INIT_POOL: (state, attr) => {
      state.objectPool = attr;
    },
    UPDATE_CANVASID: (state, id) => {
      state.currentTempId = id;
    },
    UPDATE_SELECTCHANGE: (state, selectChange) => {
      state.selectChange = selectChange;
    },

    UPDATE_TABLEFORMLIST: (state, formList) => {
      state.tableFormList = formList;
    },
    UPDATE_NODEID: (state, id) => {
      state.currentNodeId = id;
    },
    CURRENT_STATE: (state, status) => {
      state.currentStatus = status;
    },
    SAVE_DETAIL: (state, status) => {
      state.tempDetailInfo = status;
    },
    FROM_UPDATE: (state, form) => {
      state.form = form;
    },
    GENERAL_UPDATE: (state, generalProperties) => {
      state.generalProperties = generalProperties;
    },
    GLOBALVAR_UPDATE: (state, globalVariable) => {
      state.globalVariable = globalVariable;
    },
    LANUCHING_UPDATE: (state, { type, launchs }) => {
      state[type] = launchs;
    },
    CUSTOM_FORM: (state, customForm) => {
      state.customForm = customForm;
    },
    FORM_ITEM: (state, formItem) => {
      state.formItem = formItem;
    },
    ALLFIELDSDATAFUN: (state, allFieldsData) => {
      state.allFieldsData = allFieldsData;
    },
    INIT_FLOWINSTANCEVAR: (state, fiv) => {
      state.flowInstanceVariables = fiv;
    },

    UPDATE_PAGEFRESH: (state) => {
      state.pageFresh = !state.pageFresh;
    },

    UPDATEINSTRUCTIONFORM: (state, data) => {
      state.instructionFormv = data;
    },
    UPDATEMANUALFORM: (state, data) => {
      state.manualForm = data;
    },
    UPDATETIMINGFORM: (state, data) => {
      state.timingForm = data;
    }
  },
  actions: {
    saveMonitorTask({ commit }, params) {
      commit('UPDATE_MONITOR_TASK', params);
    },
    saveStationList({ commit }, params) {
      commit('UPDATE_STATION_LIST', params);
    },
    // 
    saveCanvasXml({ commit }, params) {
      commit('UPDATE_CANVAS_XML', params);
    },
    saveUserTaskNodes({ commit }, params) {
      commit('USERTASK_NODE_LIST', params);
    },
    // 
    updateDetailInfo({ commit }, params) {
      commit('SAVE_DETAIL', params);
    },
    //  params{id}
    updateAttr({ commit, state }, params) {
      const finalPool = assignObj(state.objectPool, params);
      commit('UPDATE_POOL', finalPool);
    },
    updateEditNameFlag({ commit }, params) {
      commit('UPDATE_EDITFLAG', params);
    },
    // ()attr{}
    initObjectPool({ commit }, attr) {
      commit('INIT_POOL', attr);
    },
    // idtab
    updateCurrentNodeId({ commit }, id) {
      commit('UPDATE_NODEID', id);
    },
    updateCurrentCanvasId({ commit }, id) {
      commit('UPDATE_CANVASID', id);
    },
    updateSelectChange({ commit }, selectChange) {
      commit('UPDATE_SELECTCHANGE', selectChange);
    },
    updateTableFormList({ commit }, formList) {
      commit('UPDATE_TABLEFORMLIST', formList);
    },
    // 
    getCurrentStatus({ commit }, status) {
      commit('CURRENT_STATE', status);
    },
    getCustomForm({ commit }, form) {
      commit('CUSTOM_FORM', form);
    },
    updateForm({ commit }, form) {
      commit('FROM_UPDATE', form);
    },
    getFormItem({ commit }, formItem) {
      commit('FORM_ITEM', formItem);
    },
    getallFieldsData({ commit }) {
      dataViewFields().then(({ data, error }) => {
        if (!error) {
          dataViewFields({ type: '1' }).then((res) => {
            commit('ALLFIELDSDATAFUN', [...data, ...res.data]);
          });
        }
      });
    },
    // 
    initTempDetail({ commit, state }) {
      commit('GENERAL_UPDATE', {});
      commit('SWITCH_TAB', 'canvas');
      commit('FROM_UPDATE', []);
      commit('CUSTOM_FORM', { formList: [] });
      commit('FORM_ITEM', '');
      commit('USERTASK_NODE_LIST', []);
      commit('GLOBALVAR_UPDATE', []);
      commit('INIT_POOL', {});
      commit('UPDATE_CANVAS_XML', '');
      commit('UPDATE_CANVASID', '');
      commit('UPDATE_MONITOR_TASK', []);
      commit('UPDATE_STATION_LIST', []);
      commit('LANUCHING_UPDATE', { type: 'manualLaunch', launchs: [] });
      commit('LANUCHING_UPDATE', { type: 'timingLaunch', launchs: [] });
      commit('LANUCHING_UPDATE', { type: 'instructionLaunch', launchs: [] });

      // 
      if (state.flowInstanceVariables.length === 0) {
        getFlowInstanceVariables().then(({ error, data }) => {
          if (!error) {
            commit(
              'INIT_FLOWINSTANCEVAR',
              data.map((item) => {
                return {
                  text: item.expressionName,
                  label: item.expressionName,
                  value: item.expression
                };
              })
            );
          }
        });
      }
    },
    getProcessDetail({ commit }, id) {
      processDetail({ id }).then(({ data, error }) => {
        if (!error) {
          commit('GENERAL_UPDATE', data.generalProperties || {});
          if (data.form) {
            const formBase = data.form;
            formBase.forEach((formBaseItem) => {
              if (
                Object.prototype.hasOwnProperty.call(
                  formBaseItem,
                  'extendProp'
                ) &&
                formBaseItem.extendProp
              ) {
                formBaseItem.extendProp = JSON.parse(
                  Base64.decode(formBaseItem.extendProp)
                );
              }
            });
            commit('FROM_UPDATE', data.form);
          }
          commit('GLOBALVAR_UPDATE', data.globalVariable);
          // 
          if (data.launchingMode) {
            const mode = data.launchingMode;

            commit('LANUCHING_UPDATE', {
              type: 'manualLaunch',
              launchs: mode.handworkParameterList || []
            });
            commit('LANUCHING_UPDATE', {
              type: 'timingLaunch',
              launchs: mode.timingParameterList || []
            });
            commit('LANUCHING_UPDATE', {
              type: 'instructionLaunch',
              launchs: mode.externalParameterList || []
            });
          }
        } else {
          Message.error(error.message);
        }
      });
      // 
      getByTemplateId(id).then(({ data, error }) => {
        if (!error) {
          if (data && data.content) {
            commit('FORM_ITEM', data.content);
            const tcf = JSON.parse(Base64.decode(data.content));
            // 
            tcf.formList.forEach((item) => {
              if (item.showForm) {
                delete item.showForm;
              }
            });
            commit('CUSTOM_FORM', tcf);
          }
        } else {
          Message.error(error.message);
        }
      });
    },
    saveProcessTemplate({ state }) {
      return new Promise((resolve, reject) => {
        if (state.instructionFormv) {
          Message.error('');
          reject('error');
        } else if (state.manualForm) {
          Message.error('');
          reject('error');
        } else if (state.timingForm) {
          Message.error('');
          reject('error');
        } else if (
          !state.manualLaunch.length &&
          !state.timingLaunch.length &&
          !state.instructionLaunch.length
        ) {
          Message.error('');
          reject('error');
        } else {
          const formBase = state.form;
          formBase.forEach((formBaseItem) => {
            if (
              Object.prototype.hasOwnProperty.call(formBaseItem, 'extendProp')
            ) {
              const extendString = Base64.encode(
                JSON.stringify(formBaseItem.extendProp)
              );
              if (extendString) {
                formBaseItem.extendProp = extendString;
              } else {
                formBaseItem.extendProp = '';
              }
            }
          });
          const params = {
            flowChart: state.canvasXml,
            form: formBase,
            generalProperties: state.generalProperties,
            globalVariable: state.globalVariable,
            launchingMode: {
              externalParameterList: state.instructionLaunch,
              handworkParameterList: state.manualLaunch,
              timingParameterList: state.timingLaunch
            },
            formItem: state.formItem
          };
          processAdd(params).then(({ error }) => {
            if (!error) {
              Message.success('');
              resolve('success');
            } else {
              Message.error(error.message);
            }
          });
        }
      });
    },
    editProcessTemplate({ state }) {
      return new Promise((resolve, reject) => {
        if (state.instructionFormv) {
          Message.error('');
          reject('error');
        } else if (state.manualForm) {
          Message.error('');
          reject('error');
        } else if (state.timingForm) {
          Message.error('');
          reject('error');
        } else if (
          !state.manualLaunch.length &&
          !state.timingLaunch.length &&
          !state.instructionLaunch.length
        ) {
          Message.error('');
          reject('error');
        } else {
          const params = {
            form: [],
            generalProperties: state.generalProperties,
            globalVariable: state.globalVariable
          };
          const obj = { tempId: state.currentTempId };
          Object.assign(obj, params);
          processEdit(obj).then(({ error }) => {
            if (!error) {
              Message.success('');
              resolve('success');
            } else {
              Message.error(error.message);
            }
          });
        }
      });
    },
    saveFormChange({ state }) {
      const formBase = cloneDeep(state.tableFormList);
      formBase.forEach((formBaseItem) => {
        if (
          Object.prototype.hasOwnProperty.call(formBaseItem, 'extendProp') &&
          formBaseItem.extendProp
        ) {
          const extendString = Base64.encode(
            JSON.stringify(formBaseItem.extendProp)
          );
          if (extendString) {
            formBaseItem.extendProp = extendString;
          } else {
            formBaseItem.extendProp = '';
          }
        }
      });
      const datas = {
        form: formBase,
        nodeId: state.tableFormList[0].nodeId,
        tempId: state.currentTempId
      };
      editFormProcess(datas).then((data) => {});
    },

    pageFresh({ commit }) {
      commit('UPDATE_PAGEFRESH');
    },

    updateInstructionForm({ commit }, data) {
      commit('UPDATEINSTRUCTIONFORM', data);
    },
    updateManualForm({ commit }, data) {
      commit('UPDATEMANUALFORM', data);
    },
    updateTimingForm({ commit }, data) {
      commit('UPDATETIMINGFORM', data);
    }
  }
};
export default processTemplate;
