import ajax from '@utils/ajax';

const serverPre = ajax.serverUrl;
const serverName = '/multidimensional-monitor/';

// 
// 
export async function getprocesslist(params) {
  return ajax.get(serverPre(`${serverName}processlist/find`), params);
}
// 
export async function monthlyStateQuery(params) {
  return ajax.get(serverPre(`${serverName}monthlyStateQueryData`), params);
}
// 
export async function getCalendarList(params) {
  return ajax.post(
    serverPre(`${serverName}workcalendarquery/calendar-list`),
    params
  );
}
// 
export async function delRecord(params) {
  return ajax.get(
    serverPre(`${serverName}workcalendarquery/delete-memo`),
    params
  );
}
// 
export async function addRecord(params) {
  return ajax.post(
    serverPre(`${serverName}workcalendarquery/add-memo`),
    params
  );
}
// 
export async function exportCalendar(params) {
  ajax.download(serverPre(`${serverName}workcalendarquery/export`), params);
}
// 
export async function importCalendar(params) {
  return ajax.post(
    serverPre(`${serverName}workcalendarquery/import-excel`),
    params,
    {
      'Content-Type': 'multipart/form-data'
    }
  );
}
