import ajax from '@utils/ajax';
const serverPre = ajax.serverUrl;
const sceneName = '/operation-task-server';

// 
export async function getTable(data) {
  return ajax.post(
    serverPre(`${sceneName}/scene-arrangement/queryScenes`),
    data
  );
}
// 
export async function importScenes(data) {
  return ajax.post(
    serverPre(`/operation-task-server/scene-arrangement/importScenes`),
    data
  );
}
// 
export async function exportScenes(params) {
  ajax.download(
    serverPre(`/operation-task-server/scene-arrangement/export`),
    params
  );
}
// 
export async function delScenes(data) {
  return ajax.post(serverPre(`${sceneName}/scene-arrangement/delScenes`), data);
}
