  'get|/billow-diplomatic/find': (options) => {
    const params = getUrlParams(options.url);
    const obj = arrayFind(formItemDetail, 'id', params.id);
    console.log(params);
    return {
      status: 200,
      message: 'success',
      data: obj
    };
  },

  // 
  'post|/billow-diplomatic/template-id': (options) => {
    const params = JSON.parse(options.body);
    formItemDetail.forEach((item, index) => {
      if (item.id === params.id) {
        formItemDetail.splice(index, 1, params);
      }
    });
    return {
      status: 200,
      message: 'success'
    };
  },

  // id
  'get|/billow-diplomatic/template-id/.*': (options) => {
    return {
      status: 200,
      message: 'success',
      data: arrayFind(formItemDetail, 'templateId', getId(options.url))
    };
  },

