import Layout from '@layouts/main';

export default [
  {
    path: '/reptileSetting',
    component: Layout,
    //   hiddenhidden
    // hidden:true,
    meta: {
      title: '',
      authority: 'reptileSetting',
      icon: 'auth'
    },
    order: 4,
    redirect: '/reptileSetting/',
    children: [
      {
        path: 'spiderList',
        name: 'spiderList',
        meta: {
          title: '',
          icon: 'database',
          authority: 'spiderList'
        },
        component: () => import('@views/reptileSetting/spiderList.vue')
      },
      {
        path: 'variables',
        name: 'variables',
        meta: {
          title: '',
          icon: 'template',
          authority: 'variables'
        },
        component: () => import('@views/reptileSetting/variables.vue')
      },
      {
        path: 'functions',
        name: 'functions',
        meta: {
          title: '',
          icon: 'template',
          authority: 'functions'
        },
        component: () => import('@views/reptileSetting/functions.vue')
      },
      {
        path: 'datasources',
        name: 'datasources',
        meta: {
          title: '',
          icon: 'template',
          authority: 'datasources'
        },
        component: () => import('@views/reptileSetting/datasources.vue')
      }
    ]
  }
];
