export async function statusChange(viewId, status) {
  return ajax.post(serverPre(`${serverName}/data-view/updStatus`), {
    viewId,
    status
  });
}

/**
 * 
 * @param dictFlag1 0 
 * @param status0 123
 * @param idsid
 * @param type: 0 1   0
 */
export async function dataViewFields(params) {
  if (!params) {
    params = { type: '0' };
  } else if (!params.type) {
    params.type = '0';
  }
  params.status = '0';
  return ajax.get(serverPre(`${serverName}/data-view/fields`), params);
}

