import axios from '../utils/ajax';

const serverUrl = axios.serverUrl;
// const serverUrl = axios.serverUrlMock;
const serverName = '/billow-diplomatic';

// 1.1
export async function getFormAttribute(obj) {
  return axios.get(serverUrl(`${serverName}/find?id=${obj.id}`));
}

// id
export async function getByTemplateId(templateId) {
  return axios.get(serverUrl(`${serverName}/template-id/${templateId}`));
}

// 1.2
export async function saveFormAttribute(params) {
  return axios.post(serverUrl(`${serverName}/save`), params);
}

export async function editTemp(params) {
  return axios.post(serverUrl(`${serverName}/template-id`), params);
}

//  
export async function formDetail(params) {
  return axios.get(serverUrl(`/${params.url}`), params.paramId);
}

//  
export async function saveFormData(params) {
  return axios.post(serverUrl(`/${params.saveAddress}`), params.paramsObtain);
}

//  
export async function submitFormData(params) {
  return axios.post(serverUrl(`/${params.submitAddress}`), params.paramsObtain);
}
