let actionIndex = 1;
function switchRouterToMenu(parent, routers) {
  const menus = [];
  routers.forEach((item) => {
    const meta = item.meta;
    if (!meta || !meta.authority) {
      return;
    }

    if (meta.hidden) {
      return;
    }

    if (item.path.indexOf('home') > -1) {
      return;
    }

    const menu = {};
    menu.code = meta.authority;
    menu.id = meta.authority;
    menu.title = meta.title;
    menu.icon = meta.icon;
    if (meta.href) {
      menu.href = meta.href;
    }

    if (item.children) {
      const mc = switchRouterToMenu(menu.code, item.children);
      if (mc.length > 0) {
        menu.children = mc;
      }
    } else {
      const preStr = parent !== '' ? `${parent}_` : '';
      if (meta.actions) {
        menu.actions = meta.actions.map((act) => {
          return {
            code: `${preStr}${menu.code}:${act.code}`,
            id: `action_${actionIndex++}`,
            name: act.name
          };
        });
      } else {
        if (parent) {
          menu.actions = [
            {
              code: `${parent}_${menu.code}:query`,
              id: `action_${actionIndex++}`,
              name: ''
            }
          ];
        }
      }
    }

    menus.push(menu);
  });
  return menus;
}

function initMunu() {
  actionIndex = 1;
  const menus = switchRouterToMenu('', RoutesMapConfig);
  return menus;
}
