import ajax from '@utils/ajax';

// const serverPre = ajax.serverUrlMock;
const serverPre = ajax.serverUrl;
const serverName = '/page-management';

// 
export async function getPageTemplateList(params) {
  return ajax.get(serverPre(`${serverName}/page-template/list`), params);
}

// 
export async function deletePageTemplate(params) {
  return ajax.post(serverPre(`${serverName}/page-template/delete`), params);
}

// 
export async function pageTemplateDetail(params) {
  return ajax.get(serverPre(`${serverName}/page-template/detail`), params);
}

// 
export async function savePageTemplate(params) {
  return ajax.post(serverPre(`${serverName}/page-template/edit`), params);
}

// /
export async function releasePageTemplate(params) {
  return ajax.post(serverPre(`${serverName}/page-template/release`), params);
}

// 
export async function addLayoutTemplate(params) {
  return ajax.post(serverPre(`${serverName}/page-template/addLayout`), params);
}

// 
export async function getLayoutTemplate(params) {
  return ajax.get(serverPre(`${serverName}/page-template/layoutList`), params);
}

// 
export async function deleteLayoutTemplate(params) {
  return ajax.post(
    serverPre(`${serverName}/page-template/deleteLayout`),
    params
  );
}
// 
export async function updateTableData(params) {
  return ajax.post(serverPre(`/monitor-control/mongo/update`), params);
}
